.memberWrapper {
  height: 100vh;
  &-backButton {
    display: flex;
    justify-content: flex-start;
    cursor: pointer;
  }

  &__memberInfo {
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color: lemonchiffon;
    border-radius: 8px;
    padding: 16px;
    box-sizing: border-box;
    &__mainInfo {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    &__contactInfo {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      &-social {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        &-icon {
          padding-right: 8px;
        }
      }
    }
  }
}
